"use client"

import { useEffect, useMemo, useState } from "react"
import { createClient } from "@/utils/supabase/client"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { toast } from "sonner"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

type Reserva = {
  id: string
  clinic_id: string
  patient_name: string | null
  status: string | null
  starts_at: string | null
  ends_at: string | null
  price_cents: number | null
  currency: string | null
  physio_id: string | null
  created_at: string
  physiotherapist?: { id: string; name: string } | null
}

type Physio = {
  id: string
  clinic_id: string
  name: string
  active: boolean | null
}

type Clinic = {
  id: string
  name: string
  location: string | null
}

export default function ClinicPanelPage() {
  const supabase = createClient()

  const [clinicId, setClinicId] = useState<string | null>(null)
  const [clinic, setClinic] = useState<Clinic | null>(null)
  const [reservas, setReservas] = useState<Reserva[]>([])
  const [physiosByClinic, setPhysiosByClinic] = useState<Record<string, Physio[]>>({})
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState<string | null>(null)

  // 🔹 Obtener clínica del usuario autenticado
  useEffect(() => {
    const fetchUserClinic = async () => {
      const {
        data: { user },
        error: userError,
      } = await supabase.auth.getUser()

      if (userError || !user) {
        toast.error("No se ha detectado sesión activa.")
        return
      }

      const { data: profile, error: profileError } = await supabase
        .from("profiles")
        .select("role, clinic_id")
        .eq("id", user.id)
        .single()

      if (profileError || !profile) {
        toast.error("No se pudo obtener el perfil del usuario.")
        return
      }

      if (profile.role !== "clinica") {
        toast.warning("⚠️ Solo las clínicas pueden acceder al panel.")
        return
      }

      setClinicId(profile.clinic_id)
    }

    fetchUserClinic()
  }, [supabase])

  // 🔹 Cargar información de la clínica
  useEffect(() => {
    if (!clinicId) return

    const fetchClinic = async () => {
      const { data, error } = await supabase
        .from("clinics")
        .select("id, name, location")
        .eq("id", clinicId)
        .single()

      if (error) {
        console.error(error)
        toast.error("No se pudo cargar la información de la clínica.")
      } else {
        setClinic(data)
      }
    }

    fetchClinic()
  }, [clinicId, supabase])

  // 🔹 Cargar reservas y fisios
  useEffect(() => {
    if (!clinicId) return

    const fetchReservas = async () => {
      setLoading(true)

      const { data, error } = await supabase
        .from("reservas")
        .select(`
          id, clinic_id, patient_name, status, starts_at, ends_at,
          price_cents, currency, physio_id, created_at,
          physiotherapist:physio_id ( id, name )
        `)
        .eq("clinic_id", clinicId)
        .order("starts_at", { ascending: true })

      if (error) {
        console.error("Error cargando reservas:", error.message)
        toast.error(`Error al cargar reservas: ${error.message}`)
        setReservas([])
        setLoading(false)
        return
      }

      setReservas(data as Reserva[])

      // Fisios de la clínica
      const { data: physios, error: physioErr } = await supabase
        .from("physiotherapists")
        .select("id, clinic_id, name, active")
        .eq("clinic_id", clinicId)

      if (physioErr) {
        toast.warning("No se pudieron cargar los fisioterapeutas.")
        setPhysiosByClinic({})
        setLoading(false)
        return
      }

      setPhysiosByClinic({ [clinicId]: physios ?? [] })
      setLoading(false)
    }

    fetchReservas()
  }, [clinicId, supabase])

  // 🔹 Helpers
  const formatDateTime = (iso: string | null) =>
    iso ? new Date(iso).toLocaleString("es-ES") : "—"

  const euro = (cents: number | null, currency?: string | null) => {
    if (cents == null) return "—"
    try {
      return new Intl.NumberFormat("es-ES", {
        style: "currency",
        currency: currency ?? "EUR",
      }).format(cents / 100)
    } catch {
      return `${(cents / 100).toFixed(2)} ${currency ?? "EUR"}`
    }
  }

  // 🔹 Render
  if (loading)
    return <p className="text-center p-8 text-muted-foreground">Cargando reservas…</p>

  if (!clinicId)
    return (
      <p className="text-center p-8 text-muted-foreground">
        Inicia sesión como clínica para acceder a tu panel.
      </p>
    )

  return (
    <div className="max-w-6xl mx-auto p-8">
      {/* 🏥 Encabezado de clínica */}
      {clinic && (
        <div className="mb-10 text-center">
          <h1 className="text-3xl font-bold">
            🏥 {clinic.name}
          </h1>
          <p className="text-muted-foreground text-sm">
            📍 {clinic.location ?? "Ubicación no asignada"}
          </p>
        </div>
      )}

      {reservas.length === 0 ? (
        <p className="text-center text-muted-foreground">
          Todavía no hay reservas registradas.
        </p>
      ) : (
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
          {reservas.map((r) => {
            const physios = physiosByClinic[r.clinic_id] ?? []
            return (
              <Card key={r.id} className="border shadow-md">
                <CardHeader>
                  <CardTitle className="text-lg font-semibold">
                    {r.patient_name ?? "Paciente"}
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-3 text-sm text-muted-foreground">
                  <p>
                    <strong>Estado:</strong>{" "}
                    <span className="font-medium text-foreground">
                      {r.status ?? "pending"}
                    </span>
                  </p>
                  <p>
                    <strong>Inicio:</strong> {formatDateTime(r.starts_at)}
                  </p>
                  <p>
                    <strong>Fin:</strong> {formatDateTime(r.ends_at)}
                  </p>
                  <p>
                    <strong>Precio:</strong> {euro(r.price_cents, r.currency)}
                  </p>

                  <div className="space-y-1">
                    <p className="text-foreground">
                      <strong>Fisioterapeuta:</strong>{" "}
                      {r.physiotherapist?.name ?? "Sin asignar"}
                    </p>

                    <Select
                      value={r.physio_id ?? ""}
                      onValueChange={(val) =>
                        supabase
                          .from("reservas")
                          .update({ physio_id: val === "" ? null : val })
                          .eq("id", r.id)
                      }
                      disabled={saving === r.id || physios.length === 0}
                    >
                      <SelectTrigger className="w-full">
                        <SelectValue
                          placeholder={
                            physios.length > 0
                              ? "Asignar fisioterapeuta…"
                              : "No hay fisios disponibles"
                          }
                        />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="">Sin asignar</SelectItem>
                        {physios.map((p) => (
                          <SelectItem key={p.id} value={p.id}>
                            {p.name}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <p className="text-xs text-muted-foreground/70">
                    Creada el:{" "}
                    {new Date(r.created_at).toLocaleDateString("es-ES")}
                  </p>
                </CardContent>
              </Card>
            )
          })}
        </div>
      )}
    </div>
  )
}
